@import url('https://fonts.googleapis.com/css?family=Patua+One%7CRoboto');
@import url('https://fonts.googleapis.com/css?family=Lato:300,400,700');

body {
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  font-smooth: always;
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 2rem;
  letter-spacing: 1px;
  margin: 0;
  padding: 0;
  text-align: center;
}

li{
  list-style-type: none;
  padding-left:0.7rem;
}

.app-outer {
  -ms-flex-align: center;
      align-items: center;
  bottom: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  left: 0;
  padding: 0 0;
  position: absolute;
  right: 0;
  top: 0;
}

.app-inner {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
  /* max-width: 100%; */
  width: 100vw;
}



#add-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
}

label{
  margin: .5rem;
}


.ReactModal__Content{
   height: 600px;
   width: 600px;
   padding: 10px;
   max-height: 1000px;
   max-width: 1000px;
}


/* .sc-EHOje eBYmyq {
  display: flex !important;
  flex-direction: row !important;
} */

a:-webkit-any-link {
  color: inherit !important;
  cursor: pointer;
}

.tag-cloud {
  -ms-flex: 1 1;
      flex: 1 1;
}

.tag-cloud>div {
  -webkit-transition: 1.4s;
  -o-transition: 1.4s;
  transition: 1.4s;
}

.tag-item-wrapper {
  cursor: pointer;
}

.tag-item-wrapper:hover {
  z-index: 100;
}

.tag-item-wrapper:hover .tag-item-tooltip {
  display: block;
}

.tag-item-tooltip {
  background: #c01916;
  color: white;
  margin-top: 5px;
  z-index: 100;
  display: none;
  padding: 15px;
  font-size: 14px;
  line-height: 1;
  position: absolute;
  min-width: 60px;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  text-align: center;
}


