@import url('https://fonts.googleapis.com/css?family=Patua+One%7CRoboto');
@import url('https://fonts.googleapis.com/css?family=Lato:300,400,700');

body {
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  font-smooth: always;
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 3.5rem;
  letter-spacing: 1px;
  margin: 0;
  padding-left: .5rem;
  text-align: center;
}

li{
  list-style-type: none;
  padding-left:0.7rem;
}

.app-outer {
  -ms-flex-align: center;
      align-items: center;
  bottom: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  left: 0;
  padding: 0 0;
  position: absolute;
  right: 0;
  top: 0;
}

.app-inner {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
  width: 100vw;
}

#add-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
}

label{
  margin: .5rem;
}

/* Button Styling */
#delete-button,
#update-button,
#add-button,
.modal-open {
    background: #4682b4;
    font-size: 1rem;
    border-radius: 5px;
    -webkit-transition: all 0.2s;
    -o-transition: all 0.2s;
    transition: all 0.2s;
    max-width: 170px;
    max-height: 80px !important;
    color: white;
    cursor: pointer;
    height: 50px !important;

}

#delete-button:hover,
#update-button:hover,
#add-button:hover {
  color:#3b5d7e;
  background-color: white;
  border: 1px solid #3b5d7e;
  cursor: pointer;
  font-size: 1rem;

}


.ReactModal__Content{
   height: 600px;
   width: 600px;
   padding: 10px;
   max-height: 1000px;
   max-width: 1000px;
}


