a:-webkit-any-link {
  color: inherit !important;
  cursor: pointer;
  text-decoration: none !important;
}

.tag-cloud {
  -ms-flex: 1 1;
      flex: 1 1;
}

.tag-cloud>div {
  -webkit-transition: 1.4s;
  -o-transition: 1.4s;
  transition: 1.4s;
}

.tag-item-wrapper {
  cursor: pointer;
}

.tag-item-wrapper:hover {
  z-index: 100;
}

.tag-item-wrapper:hover .tag-item-tooltip {
  display: block;
}

